header{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    width: 100%;
    height: 10rem;
    //background-color: $white ;
    display: flex;
    transition: all 0.3s;
    .container {
        @include flex(row, center, space-between);
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        .logo {
            width: 100%;
            max-width: 13rem;
            height: auto;
        }
    }
    &.fixed-menu {
        height: 7rem;
        transition: all 0.3s;
        background-color: rgba(255, 255, 255, 0.93);
        .container {
            .logo {
                max-width: 10rem;
            }
            nav {
                ul {
                    li {
                        a {
                            span {
                                color: $cinza-textos;
                            }
                        }
                    }
                }
            }
        }
    }
    
    nav {
        position: relative;
        ul {
            display: flex;
            li {
                display: flex;
                align-items: center;
                &:not( :first-child) {
                    margin-left: 5.6rem;
                }
            }
            a {
                display: flex;
                align-items: center;
                span {
                    font-size: 1.4rem;
                    margin-left: 1.4rem;
                    line-height: 1.8rem;
                    color: #fff;
                    margin-right: 1.2rem;
                    font-weight: 500;
                    letter-spacing: 0.5px;
                }
            }
        }
        .dropdown {
            position: absolute;
            top: 75px;
            left: -100px;
            width: 100%;
            max-width: 35rem;
            background: #ffffff;
            border-radius: 6px;
            opacity: 0;
            transition: all .3s;
            pointer-events: none;
            transform: translateY(20px);
            &:after {
                content: "";
                width: 18rem;
                height: 5px;
                background: $secondary;
                border-radius: 12px 12px 0 0;
                position: absolute;
                bottom: 0;
            }
            &.active {
                opacity: 1;
                pointer-events: all;
                transform: translateY(0);
            }
            a {
                .item {
                &:not(:last-child){
                    margin-bottom: 2.8rem;
                    padding-bottom: 20rem;
                }
                strong {
                    font-weight: 600;
                    font-size: 1.6rem;
                    line-height: 2rem;
                    color: #5f5c6b;
                    display: block;
                    padding: 2rem 4rem;
                    &:not(:last-child){
                        border-bottom: 1px solid $cinza-fundos;
                    }
                    &:hover {
                        transition: 0.3s;
                        background-color: $cinza-textos;
                        color: #fff;
                    }
                }
            }

            }
            
        }
    }
}

