* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: none;
    outline: none;
    list-style: none;
    text-decoration: none;
    font-family: 'Open Sans', sans-serif;
}

h1, h2, h3, h4, h5 {
    font-family: 'Exo', sans-serif;
    letter-spacing: -0.5px;
    font-style: normal;
}

h2 {
font-weight: 700;
font-size: 40px;
line-height: 112%;
}

h3 {
    font-weight: 800;
    font-size: 3rem;
    line-height: 120%;
    //margin-bottom: 2rem;
}


h4 {
    font-weight: 800;
    font-size: 20px;
    line-height: 27px;
    color: #323841;
}

h5 {
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #323841;
}

p {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 150%;
    color: #70738A;
    //margin-bottom: 3rem;
}

img {
    max-width: 100%;
}

html {
    font-size: 62.5%;
    @include responsive(1280px){
        font-size: 55%;
    }
}
