footer {
    padding-top: 4.9rem;
    background-color: #323841;
    .container {
        .top-footer { 
            @include flex (row, center, space-between);
            border-bottom: 2px solid rgba(73,196,253,0.2);
            padding-bottom: 4.8rem;
            margin-bottom: 5.7rem;
            .social {
                @include flex (row, center, space-between);
                span {
                    font-family: "Open Sans", sans-serif;
                    font-size: 1.6rem;
                    line-height: 1.6rem;
                    text-align: right;
                    color: #FFFFFF;
                    opacity: 0.7;
                    margin-right: 3rem;
                }
                ul {                        
                    @include flex (row, center, center);
                    a {
                        &:not(:last-child){
                            margin-right: 3rem;
                        }
                    }
                }
            }
        }
        .main-footer {
            @include flex (row, flex-start, space-between);
            border-bottom: 2px solid rgba(73,196,253,0.2);
            padding-bottom: 6rem;
            margin-bottom: 3.9rem;
            nav {                    
                @include flex (row, flex-start, space-between);
                width: 100%;
                max-width: 85.5rem;
                .item {
                    strong {
                        font-family: "Exo", sans-serif;
                        font-weight: 600;
                        font-size: 1.8rem;
                        line-height: 5rem;
                        color: #FFFFFF;
                    }
                }
                ul {
                    li {
                        &:not(:last-child){
                            margin-bottom: 3rem;
                        }
                        &:first-child{
                            margin-top: 2.1rem;
                        }
                        a {
                            font-family: "Open Sans", sans serif;
                            font-size: 1.6rem;
                            line-height: 1.4rem;
                            color: #FFFFFF;
                            opacity: 0.67;
                        }

                    }
                }
            }
        }
        .contact {
            width: 100%;
            max-width: 28rem;
            a {
                &:not(:last-child){
                    margin-bottom: 2.4rem;
                }
                @include flex (row, center, flex-start);
                padding: 1.7rem 2.3rem;
                background: rgba(0, 165, 240, .23);
                border-radius: 6px;
                .info {
                    strong {
                        font-weight: 600;
                    }
                    margin-left: 2.4rem;
                    font-family: "Exo", sans-serif;
                    font-size: 1.6rem;
                    line-height: 150%;
                    color: #FFFFFF;
                    p {
                        color: #f2f2f2;
                        opacity: 0.67;
                    }
                }
            }
        }
    }
    .msg {
        border-bottom: 4px solid $orange;
        padding-bottom: 3.8rem;

        p {
            width: 100%;
            max-width: 66rem;
            font-weight: normal;
            font-size: 1.6rem;
            line-height: 150%;
            color: #FFFFFF;
            opacity: 0.8;
        }
    }
}
