.s-institucional {
    padding-top: 7rem;
    .container {
        @include flex (row, center, space-between);
        .video-institucional {
            position: relative;
            background-color: #006D8F;
            width: 100%;
            max-width: 60rem;
            height: 40rem;
            border-radius: 20px;
            overflow: hidden;
            .play {
                background: url(/img/video-thumb.png) no-repeat;
                max-width: 100%;
                width: 60rem;
                height: 40rem;
                background-size: cover;
                width: 100%;
                height: 100%;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                -webkit-box-pack: center;
                -ms-flex-pack: center;
                justify-content: center;
                cursor: pointer;
                .player {
                    background: url(/img/play.svg) no-repeat;
                    background-size: cover;
                    width: 72px;
                    height: 72px;
                    -webkit-transform: scale(1);
                    -ms-transform: scale(1);
                    transform: scale(1);
                    -webkit-transition: all .3s;
                    -o-transition: all .3s;
                    transition: all .3s;
                    &:hover {
                        -webkit-transform: scale(1.2);
                        -ms-transform: scale(1.2);
                        transform: scale(1.2);
                        -webkit-transition: all .3s;
                        -o-transition: all .3s;
                        transition: all .3s;
                    }
                }
                &:after {
                    content: "vídeo institucional";
                    position: absolute;
                    bottom: 120px;
                    font-family: barlow;
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 13px;
                    letter-spacing: 3px;
                    text-transform: uppercase;
                    color: #fff;
                    font-family: 'Exo', sans-serif;
                }
            }
        }
        
        .texto-institucional {
                padding-bottom: 7rem;

            .text {
                width: 100%;
                max-width: 55rem;
                margin-bottom: 2.8rem;
                p {
                    font-family: 'Open Sans', sans-serif;
                    font-weight: 400;
                    font-size: 1.6rem;
                    line-height: 150%;
                    color: #627185; 
                    margin-bottom: 2.4rem;
                }
            }
        }
    }
}

.s-solucoes {
    background: linear-gradient(102.53deg, rgb(1, 45, 114) 13.3%, rgb(32, 40, 137) 47.04%, rgb(35, 203, 203) 124.98%);
    .container{
        padding-top: 9.6rem;
        padding-bottom: 8rem;
        @include flex(row, flex-start, space-between);
        .cards-solucoes{
            ul {
                @include flex(row, flex-start, space-between);
                flex-wrap: wrap;
                max-width: 100%;
                li {
                    max-width: 38.5rem;
                    height: 25rem;
                    margin-bottom: 3rem;
                    background-color: white;
                    padding: 3.5rem;
                    border-radius: 10px;
                    -webkit-transform: scale(1);
                    -ms-transform: scale(1);
                    transform: scale(1);
                    -webkit-transition: all .3s;
                    -o-transition: all .3s;
                    .icon {
                        margin-bottom: 1.5rem;
                        img{
                            border-bottom: 2px solid $orange;
                            padding-bottom: 1.6rem;
                        }
                    }
                    h4 {
                        margin-bottom: 1.5rem;
                    }
                    p {
                        line-height: 150%;
                        font-size: 1.6rem;
                        color: $gray-brand;
                    }

                    &:hover {
                        background-color: #f2f2f2;
                        transition: all .3s;
                        -webkit-transform: scale(1.05);
                        -ms-transform: scale(1.05);
                        transform: scale(1.05);
                        -webkit-transition: all .3s;
                        -o-transition: all .3s;
                    }
                }
                .card-inicial {
                    background-color: transparent;
                    padding: inherit;
                    &:hover {
                        background-color: inherit;
                        -webkit-transform: none;
                        -ms-transform: none;
                        transform: none;
                        cursor: default;
                    }
                    .icon {
                        img {
                            color: white;
                        }
                    }
                    h4 {
                        font-weight: 500;
                        font-size: 3.6rem;
                        line-height: 120%;
                        color: white;
                        span {
                            font-weight:700;
                        }
                    }
                    p {
                        color: white;
                        font-size: 2rem;
                    }
                }
                
                .card-final {
                    text-align: center;
                    background-color: $orange;
                    &:hover {
                        background-color: $orange;
                        -webkit-transform: none;
                        -ms-transform: none;
                        transform: none;
                        cursor: default;
                    }
                    .icon {
                        display: none;
                    }
                    h4 {
                        line-height: 120%;
                        color: white;
                        span {
                            font-weight:500;
                        }
                    }
                    p {
                        color: white;
                        margin-bottom: 2rem;
                    }
                }
            }
        }
        .image {
            img{
                position: relative;
                top: -16.8rem;
            }
        }
    }
}
