//modal

.modal {
    position: fixed;
    top: 30%;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    opacity: 0;
    transition: all .3s;
    pointer-events: none;
    .overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);        
        z-index: 0;
    }
    .box {
      position: relative;
      z-index: 201;
      width: 94rem;
      height: 53rem;
      @include flex (row, center, center); 
      background-color: #f7f7f7;
      //border-radius: 10px;
      transform: translateY(20px);
      transition: transform .3s;
      //overflow: hidden;
      .close {
          position: absolute;
          top: -51px;
          right: 0;
          width: 10rem;
          height: 4rem;
          background-color: #fff;
          border: 1px solid #000;
          font-weight: bold;

      } 
    }
    &.active {
        opacity: 1;
        pointer-events: all;
        .box {
            transform: translateY(0);
        }
    }
}
