@mixin responsive ($ScreenWidth) {
    @media (max-width: $ScreenWidth) {
        @content;
    }
}

@mixin flex($direction, $align, $justify){
    display: flex;
    flex-direction: $direction;
    align-items: $align;
    justify-content: $justify;
}


@mixin style-btn($height, $bg, $colorText){
    display: inline-block;
    height: $height;
    line-height: $height;
    background-color: $bg;
    border-radius: 10px;
    padding: 0 4rem;
    font-size: 1.6rem;
    cursor: pointer;
    transition: all 0.3s;
    color: $colorText;
    font-family: 'Open Sans', sans-serif;
}